.slick-prev, .slick-next {
  z-index: 500;
  width: 34px;
  height: 28px;
  color: rgba(0, 0, 0, 0);
  background-color: #fff;
  border: 0;
  border-radius: 4px;
  padding: 8px 10px;
  transition: all .3s ease-out;
  position: absolute;
  top: 50%;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .06);
}

