  left: -8px;
}

.slick-prev:before {
  content: "";
  width: 13px;
  height: 13px;
  border-top: 2px solid #2c2c2c;
  border-left: 2px solid #2c2c2c;
  transition: all .3s ease-out;
  display: block;
  transform: rotate(-45deg);
}

