    padding: 12px 19px;
    left: -16px;
  }
}

.slick-next {
  right: -8px;
}

.slick-next:before {
  content: "";
  width: 13px;
  height: 13px;
  border-top: 2px solid #2c2c2c;
  border-right: 2px solid #2c2c2c;
  transition: all .3s ease-out;
  display: block;
  transform: rotate(45deg);
}

@media screen and (min-width: 1280px) {
